@import url(https://fonts.googleapis.com/css2?family=Jura&display=swap);
html, body {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	font-family: "Jura", sans-serif;
	font-size: 15px;
}

html {
	overflow-y: scroll;
}

body {
	display: flex;
}

select:focus, input:focus {
	outline: none;
}

#root {
	display: flex;
	flex: 1 1;
	width: 100%;
}

.pageContainer {
	display: flex;
	flex-direction: column;
	flex: 1 1;
	width: 100%;
}

.page {
	flex: 1 1;
	display: flex;
	flex-direction: column;
	margin-bottom: 50px;
}

h1 {
	margin: 20px 10px;
	font-size: 38px;
}

.card {
	border: solid 1px rgb(200,200,200);
	box-shadow: 0px 1px 5px 2px rgba(0, 0, 0, 0.10);
	margin: 15px 10px;
	background-color: white;
	border-radius: 8px;
}

.floatingButton {
	position: fixed;
	bottom: 70px;
	right: 5%;
	width: 40px;
	height: 40px;
	border-radius: 25px;
	border: 3px solid rgb(18 179 164);
	color: rgb(248 156 50);
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 24px;
	font-weight: bold;
	cursor: pointer;
	box-shadow: 0px 1px 6px 2px rgba(0, 0, 0, 0.15);
}

.loading {
	flex: 1 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.loginPage {
	flex: 1 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.loginPage .icon {
	width: 140px;
	margin: 20px;
}

.loginPage .icon path {
	fill: rgb(118 185 0);
	stroke: rgb(40 151 40);
	stroke-width: 0.5px;
}

.loginPage .content {
	width: 400px;
	margin: 20px;
	color: rgb(132 133 137);
}

.loginPage button {
	margin: 20px;
	padding: 13px 40px;
	background-color: rgb(40 151 40);
	border-radius: 5px;
	border: 0;
	color: rgb(226 226 226);
	font-weight: bold;
	font-size: 16px;
	cursor: pointer;
}

.loginPage .update {
	margin: 20px;
	color: #FF7200;
	font-weight: bold;
}
.navContainer {
	position: fixed;
	bottom: 0px;
	left: 0px;
	right: 0px;
	height: 50px;
	display: flex;
	justify-content: space-around;
	align-items: center;
	background-color: white;
}

.navContainer .item {
	cursor: pointer;
}

.navContainer .item svg {
	height: 30px;
}

.workoutsContainer {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	flex: 1 1;
	margin: 0px 10px;
}

.workoutContainer {
	margin: 10px 0px;
	border: solid 1px;
	border-radius: 20px;
	padding: 18px;
	display: flex;
	flex-direction: column;
	min-width: 500px;
}

.headerContainer {
	display: flex;
	align-items: center;
}

.itemBar {
	width: 6px;
	border-radius: 6px;
	margin-right: 10px;
	height: 100%;
}

.dataContainer {
	flex: 1 1;
}

.workoutHeader {
	font-size: 1.4em;
	font-weight: bold;
}

.workoutSubHeader {
